.dropzone {
  box-sizing: border-box;

  position: relative;
  width: 100%;
  height: 192.92px;
  // left: 404px;
  // top: 408px;
  padding: 0px 15px;
  background: #fbfbfb;
  border: 0.884956px dashed #d9d9d9;
  border-radius: 3.53982px;
}
.cloud_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0%;
  bottom: 40%;
  left: 0%;
  right: 0%;
}
.drag_text p {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 10%;
  bottom: 0%;
  left: 0%;
  right: 0%;

  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 14px;

  color: #7e7e7e;
}

.drag_btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 60%;
  bottom: 0%;
  left: 0%;
  right: 0%;
}

.preview_image {
  position: absolute;
  top: 0%;
  object-fit: cover;
  bottom: 0%;
  left: 0%;
  right: 0%;
  width: 100%;
  height: 100%;
  // padding: 15px;
  // border-radius: 10px !important;
}
